/*
=====================================
Button styles
=====================================
*/
.button-style {
  background: transparent;
  display: inline-block;
  margin: 1rem 0;
  padding: 0;
  border: 2px solid #0088cc;
  color: #0088cc;
  padding: 0.75rem 1.5rem 0.75rem 3rem;
  height: auto;
  line-height: 0.8rem;
  font-size: 0.8rem;
  border-radius: 25px;
  position: relative;
}
.button-style i {
  margin-right: 0.5rem;
  background: #0088cc;
  border-radius: 50%;
  color: #fff;
  font-size: 0.5rem;
  width: 1.25rem;
  height: 1.25rem;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  left: 1.25rem;
  top: calc(50% - 0.625rem);
  transition: 0.3s ease-in-out;
}
.button-style.button-primary {
  border: 2px solid #0088cc;
  color: #0088cc;
}
.button-style.button-primary i {
  background: #0088cc;
  color: #fff;
  box-shadow: 0 0 0.5rem 1px #0088cc;
}
.button-style.button-primary:hover, .button-style.button-primary.active {
  color: #fff;
  border-color: #0088cc;
  background: #0088cc;
}
.button-style.button-primary:hover i, .button-style.button-primary.active i {
  background: #fff;
  color: #0088cc;
  box-shadow: none;
  transform: translateX(-0.5rem);
}
.button-style.button-light {
  border: 2px solid #fff;
  color: #fff;
}
.button-style.button-light i {
  background: #fff;
  color: #0088cc;
  box-shadow: 0 0 0.5rem 1px #fff;
}
.button-style.button-light:hover, .button-style.button-light.active {
  color: #0088cc;
  border-color: #fff;
  background: #fff;
}
.button-style.button-light:hover i, .button-style.button-light.active i {
  background: #0088cc;
  color: #fff;
  box-shadow: none;
  transform: translateX(-0.5rem);
}
.button-style.button-dark {
  border: 2px solid #1C2023;
  color: #1C2023;
}
.button-style.button-dark i {
  background: #1C2023;
  color: #fff;
  box-shadow: 0 0 0.5rem 1px #1C2023;
}
.button-style.button-dark:hover, .button-style.button-dark.active {
  color: #fff;
  border-color: #1C2023;
  background: #1C2023;
}
.button-style.button-dark:hover i, .button-style.button-dark.active i {
  background: #fff;
  color: #1C2023;
  box-shadow: none;
  transform: translateX(-0.5rem);
}

/*
=====================================
Parallax
=====================================
*/
.parallax-container {
  z-index: 2;
}
.parallax-container::before {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  right: 0;
  z-index: 1;
}

/*
=====================================
Swiper style
=====================================
*/
.swiper {
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.swiper .swiper-slide {
  text-align: center;
  /* Center slide text vertically */
  display: -webkit-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  -webkit-align-items: center;
  align-items: center;
}
.swiper .swiper-slide img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

/*
=====================================
Diagonal borders
=====================================
*/
.diagonal-border::after {
  content: "";
  width: calc(100vw + 20rem);
  height: 10rem;
  background: #fff;
  position: absolute;
  right: -3rem;
  bottom: -8rem;
  transform: rotate(-2deg);
  z-index: 3;
}

